.archive {
	&__wrapper {
		@extend .blogContainer__wrapper;
	}
	&__blogPost {
		width: 100%;
	}
	&__blogContainer {
		@include mq(large) {
			width: 65%;
		}
	}
	&__header {
		font-size: 2.5rem;
		color: $secondary-color;
		opacity: 0.8;
	}
	
	&__blogPost {
		@extend .blogContainer__blogArticle;
	}
.blogPost__title {
	a:hover {
		color: darken($secondary-color, 15%);
	}
}
}
